<template>
<div class="homeSwiper3">
  <swiper :options="swiperOption">
    <swiper-slide v-for="slide in homeSwiper3" :key="slide.title">
      <!-- <img src="http://www.sunqiongyao.com/headerBg1.png" alt=""> -->
      <p>{{slide.title}}</p>
    </swiper-slide>
  </swiper>
</div>
</template>

<script>
import { homeSwiper3 } from "~/data/homeSwiper3";

export default {
  data() {
    return {
      swiperOption: {
        pagination: ".swiper-pagination",
        loop: false,
        slidesPerView: 2.1,
        slidesPerColumn: 1,        
        paginationClickable: true,
        spaceBetween: 5
      },
      homeSwiper3:homeSwiper3
    };
  },
  mounted() {},
  components: {
  }
};
</script>

<style lang="scss" scoped>
.swiper-container {
  width: 100%;
}
.swiper-slide {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  background:#6a3cbc;
    border-radius: 2px;
  color:#fff;
  height:120px;
  p{
    font-size:15px;
    font-weight:bold;
  }
}
</style>



