<template>
<div class="homeSwiper5">
        <swiper :options="swiperOption">
            <swiper-slide v-for="slide in homeSwiper5" :key="slide.title">
              <div class="product-text">
                <p>{{ slide.title }}</p>
                <p>{{ slide.icon }}</p>
              </div>
            </swiper-slide>
        </swiper>
    </div>
</template>

<script>
import { homeSwiper5 } from "~/data/homeSwiper5";

export default {
  data() {
    return {
      swiperOption: {
        loop: false,
        setWrapperSize: true,
        paginationClickable: true,
        mousewheelControl: true,
        observeParents: true,
        slidesPerView: "2.2",
        slidesPerColumn: 2,
        spaceBetween: 5
      },
      homeSwiper5: homeSwiper5
    }
  },
  mounted() {},
  
};
</script>

<style lang="scss" scoped>
.homeSwiper5{
  img {
    width: 100%;
    position: absolute;
    z-index: -1;
  }
  .swiper-slide {
    background: #6a3cbc;
    border-radius: 4px;
    overflow: hidden;
    color: #fff;
    height:80px;
    .product-text {
      padding: 5px 10px;
      p:nth-child(1) {
        font-size: 15px;
      }
      p:nth-child(2) {
        font-size: 12px;
        font-weight: 200;
        padding: 3px 0;
      }
    }
  }
}

</style>



