<template>
  <div class="home">
   <homeSwiper1></homeSwiper1>
   <div class="product-noTitle">
    <homeSwiper2></homeSwiper2>
   </div>

    <div class="product-title">
     <p>我的设计</p>
    <homeSwiper3></homeSwiper3>
   </div>

   <div class="product-title">
     <p>我的产品</p>
    <homeSwiper4></homeSwiper4>
   </div>

   <div class="product-title">
     <p>我的经历</p>
    <homeSwiper5></homeSwiper5>
   </div>

  </div>
</template>

<script>
import homeSwiper1 from "./homeSwiper1.vue";
import homeSwiper2 from "./homeSwiper2.vue";
import homeSwiper3 from "./homeSwiper3.vue";
import homeSwiper4 from "./homeSwiper4.vue";
import homeSwiper5 from "./homeSwiper5.vue";

export default {
  data() {
    return {};
  },
  components: {
    homeSwiper1,
    homeSwiper2,
    homeSwiper3,
    homeSwiper4,
    homeSwiper5
  },
  methods: {
    menu() {
      window.scrollTo(0,0);
    }
  },
  created(){
    this.menu();
  }
};
</script>

<style lang="scss" scoped>
.home {
  background: #f3f3f3;
  .product-noTitle {
    margin-top: 5px;
    background: #fff;
    padding: 0 5px;
  }
  .product-title {
    margin-top: 5px;
    padding: 30px 0 15px 20px;
    background: #fff;
    p {
      font-size: 20px;
      font-weight: bold;
      margin-bottom: 10px;
    }
  }
}
</style>



