<template>
<div class="homeSwiper4">
  <swiper :options="swiperOption">
    <swiper-slide v-for="slide in homeSwiper4" :key="slide.title">
      <img :src="slide.img" alt="">
      <p>{{slide.title}}</p>
    </swiper-slide>
  </swiper>
</div>
</template>

<script>
import homeProduct4 from "~/components/homeProduct4.vue";
import { homeSwiper4 } from "~/data/homeSwiper4";

export default {
  data() {
    return {
      swiperOption: {
        pagination: ".swiper-pagination",
        loop: false,
        slidesPerView: 3.5,
        slidesPerColumn: 2,
        paginationClickable: true,
        spaceBetween: 10
      },
      homeSwiper4: homeSwiper4
    };
  },
  mounted() {},
  components: {
    homeProduct4
  }
};
</script>

<style lang="scss" scoped>
.swiper-container {
  width: 100%;
}
.swiper-slide {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  position: relative;
  border: 1px solid rgba(130, 83, 215, 0.1);
  border-radius: 3px;
  overflow: hidden;

  p {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    color: #fff;
    line-height: 80px;
    font-weight: bold;
  }
  img {
  }
}
</style>



