<template>
<div class="homeSwiper1">
  <swiper :options="swiperOption">
    <swiper-slide v-for="slide in homeSwiper1" :key="slide.title">
      <p>{{slide.title}}</p>
    </swiper-slide>
  </swiper>
</div>
</template>

<script>
import homeProduct1 from "~/components/homeProduct1.vue";
import { homeSwiper1 } from "~/data/homeSwiper1";

export default {
  data() {
    return {
      swiperOption: {
        autoplay: 3000,
        pagination: ".swiper-pagination",
        loop: true,
        centeredSlides: true,
        slidesPerView: 1.1,
        slidesPerColumn: 1,
        paginationClickable: true,
        spaceBetween: 3
      },
      homeSwiper1: homeSwiper1
    };
  },
  mounted() {},
  components: {
    homeProduct1
  }
};
</script>

<style lang="scss" scoped>
.swiper-slide {
  width: 60%;
  text-align: center;
  background: #6a3cbc;
  overflow: hidden;
  height: 300px;
  border-radius: 2px;
}
.homeSwiper1 {
  position: relative;
  img {
    width: 100%;
  }
  p {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 50px;
    line-height: 50px;
    color: #fff;
    overflow: hidden;
    background: rgba(0, 0, 0, 0.5);
  }
}
</style>



